.pokedex {
  display: flex;
  overflow: hidden;
  align-items: center;
  flex: 1 1 100%;
  flex-direction: column;
  min-height: 100%;
  padding: 140px 40px 20px;
  background: white;
  box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);

  .pokedex__pokemon-card-list {
    display: grid;
    flex: 1 1 auto;
    justify-content: center;
    width: 100%;
    height: 100%;
    grid-template-rows: min-content;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    grid-gap: 40px;
  }

  .pokedex__load-button {
    font-size: 20px;
    font-weight: 800;
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-top: 40px;
    padding: 20px;
    color: white;
    border-radius: 20px;
    background: #53a4cf;
    justify-self: flex-end;

    &:disabled {
      cursor: not-allowed;
      opacity: 0.5 !important;
      background: #7c7c7c;
    }

    .button_text {
      margin-right: 10px;
    }

    svg {
      width: 30px;
      height: 30px;
    }
  }
}

@media screen and(max-width: 767.98px) {
  .pokedex {
    padding: 140px 10px 10px;

    .pokedex__pokemon-card-list {
      gap: 20px;
    }

    .pokedex__load-button {
      margin-top: 20px;
    }
  }
}

